export default {
  // 命名空间
  namespaced: true,
  state () {
    return {
      // 初始化时，将本地存储的token值赋值给vuex中的token
      token: window.localStorage.getItem('token')
    }
  },
  mutations: {
    setToken (state, token) {
      state.token = token
      // 将token值存储到本地
      window.localStorage.setItem('token', token)
    }
  },
  actions: {},
  getters: {}
}
