import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
// 调用vantui
import '@/vantui'

// 导入全局样式
import '@/assets/style/index.less'

// 导入插件
import geekIcon from '@/components'
Vue.use(geekIcon)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
